import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY, gen16CodeId, updStore, hasWritePrivilege } from "../../../stores/Utils";
import {  get_inventory_by_prod, delete_inventory } from "../services/server_fetching";
import { useNavigate } from "react-router";
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import NoPrivilege from "./NoPrivilege";
import { SvgIcon_Cmpt } from "../../../components";
import DeleteAlertInventory from "./deleteAlertInventory";
const Svg = SvgIcon_Cmpt();



const ViewComprobante  = ({ data, item }) => {

	const [obs, set_obs] = useState(0);


	
	useEffect(() => {
		updStore("inv_products_list", null);
		updStore("inv_products_stock",null);
		updObs();
		get_inventory_by_prod(item.productId,updObs)
	},[item.productId]);

	const updObs =  () => {
		set_obs(gen16CodeId())
	}




	const navigate = useNavigate();

	const close =  (element) => {
		CloseModal({id:data.modalID});
		
	}
	
	let c_p = !isNaN(getStore("inv_products_stock")?.amount / getStore("inv_products_stock")?.qty)?getStore("inv_products_stock")?.amount / getStore("inv_products_stock")?.qty:0;
	let stock = !isNaN(getStore("inv_products_stock")?.qty)?getStore("inv_products_stock")?.qty:0;

	return (
		<div style={{
			maxHeight: "80vh",
			width: "960px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "SubMayor de Producto"}
				</p>
			</div>

			<div className=" acc_dtls subMheader" style={{marginTop: 10}}>
				<div className="_dsplFlx"  >
					<div className="code" style={{color: "#646cff"}}>
						{item?.name} 
					</div>
					<div className="name">
						{item?. unit}
					</div>
					<div className="flexSpace"/>
					<div className="_dsplFlx"  >
						<div className="code" style={{color: "var(--hrm_palette-item_coll_gray)"}}>
							{"Stock:"} 
						</div>
						<div className="name" style={{color: "var(--hrm_palette-inverse-background-b)"}}>
							{stock?.toFixed(2)} 
						</div>
					</div>
					<div className="lineV" />
					<div className="_dsplFlx"  >
					<div className="code" style={{color: "var(--hrm_palette-item_coll_gray)"}}>
							{"Precio Costo:"} 
						</div>
						<div className="name" style={{color: "var(--hrm_palette-inverse-background-b)"}}>
							${c_p?.toFixed(2)} 
						</div>
					</div>
				</div>
			</div>
			
			
			<div className="_dsplFlx cmp_dtls">
				
				<div className="flexSpace"/>
				<div className="_dsplFlx"  >
					
				</div>
			</div>




			
			<div className="_dsplFlx hdr_bar purple">
				<div className="tr_bar_20">
					Comprobante
				</div>
				<div className="tr_bar_20">
					Fecha
				</div>
				<div className="tr_bar_20">
					Documento
				</div>
				<div className="tr_bar_15">
					Qty
				</div>
				<div className="tr_bar_15">
					Precio
				</div>
				<div className="tr_bar_5">
				</div>
			</div>
			
			<div className="scollVh subMHeight">
			{getStore("inv_products_list") && getStore("inv_products_list")?.map((element) => <ListingProducts key={element.inventoryId} element={element} idKey={element.inventoryId} close={close} />)}
			</div>
		</div>
	);
};

export default ViewComprobante;



const ListingProducts  = ({ element, idKey, close }) => {

	const deleteComprobante =  () => {
		delete_inventory(idKey, close);
	}

	const OpenDeleteAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(hasWritePrivilege()){
			if( Date.now() > lastTimeCallModal){
				updStore("lastTimeCallModal", Date.now()+320);
				let modaldata = {};
				modaldata["zIndex"] = 450;
				modaldata["height"] = "40vh";
				// data['observeResize']=true;
				modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
				modaldata["content"] = <DeleteAlertInventory confirm={deleteComprobante} item={element} />
				OpenModal(modaldata);
			}
		}else{
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <NoPrivilege />
			OpenModal(modaldata);
		}
	}

	
	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar_20 comprobante_id">
				{element.comprobanteId}
			</div>
			<div className="tr_bar_20">
				{element.date && parseDateMMDDYYYY(element.date)}
			</div>
			<div className="tr_bar_20">
				{element.document}
			</div>
			<div className="tr_bar_15">
				{(element.qty*1)?(element.qty*1)?.toFixed(2):""}
			</div>
			<div className="tr_bar_15">
				{(element.price*1)?(element.price*1)?.toFixed(2):""}
			</div>
			<div className="tr_bar_5"/>
			{hasWritePrivilege()?
			<div className="tr_bar_5">
				<div className="icon" onClick={OpenDeleteAlert}>
					<Svg name={"outline_delete"} color={"#c62828"}/>
				</div>
			</div>
			:null}
		</div>
	)

}




